.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    background-color: #FFF;
    height: 3em;
    width: 100vw;
}

.header nav {
    text-align: left;
}

.header ul {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    margin: 0;
    padding: 0;
}

.header a {
    text-transform: lowercase;
    text-decoration: none;
    color: #CDD0D0;
    padding: 25px;
    display: inline-block;
}

.header a:hover {
    color: #adafaf;
}

/** Material
 */
.header {
    -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 1px 5px 0 rgba(0,0,0,0.12),0 3px 1px -2px rgba(0,0,0,0.2);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,0.2), 0 2px 5px 0 rgba(0,0,0,0.19);
}


